@import url("https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;600;700&display=swap");
@font-face {
    font-family: valorantFont;
    src: url(./../resources/valorantFont/font.ttf);
}
.header {
    position: absolute;
    height: 50vh;
    width: 100vw;
    font-family: "Rubik", sans-serif;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: space-between;
    color: white;
    transform: translateY(-40vh);
    transition: all 1s 0.1s;
    z-index: 10;
}
.header-head {
    height: 55vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 7vh;
    align-items: center;
    background-color: white;
    border-bottom-left-radius: 5rem;
    border-bottom-right-radius: 5rem;
    outline: solid;
    outline-color: red;
    outline-width: 2px;
    outline-offset: -0.5rem;
    padding: 0.5rem;
    padding-top: 2rem;
}
.header-head-maps {
    height: 30vh;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}
.maps-select {
    /* background-color: white; */
    height: 30vh;
    display: flex;
    justify-content: space-evenly;
    flex-direction: column;
    align-items: center;
    overflow: hidden;
    width: 30vw;
    transition: all 0.1s 0.1s;
}
.maps-select:hover {
    cursor: pointer;
    transform: scale(1.02);
    transition: all 0.1s 0.1s;
}
.maps-select-header {
    color: white;
    text-align: center;
    font-family: "valorantFont", sans-serif;
    padding-top: 1rem;
    height: 4rem;
    font-size: 3rem;
    font-weight: 500;
    border-top-right-radius: 3rem;
    border-top-left-radius: 3rem;
    width: 90%;
    background-color: black;
    outline: solid;
    outline-color: red;
    outline-offset: -0.5rem;
    outline-width: 1px;
}
.map-select-body {
    height: 30vh;
    width: 90%;
    background-color: white;
    /* border-bottom-right-radius: 2rem; */
    border-bottom-left-radius: 2rem;
    overflow: hidden;
}
.map-select-body-image {
    width: 100%;
}

.logo-div {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 1rem;
    padding-left: 3rem;
    padding-right: 3rem;
    height: 8vh;
}
.logo-div-image {
    height: 100%;
}

.slide-down {
    height: 5vh;
    background-color: rgb(0, 0, 0);
    width: 15%;
    align-self: center;
    border-bottom-left-radius: 6rem;
    border-bottom-right-radius: 6rem;
    display: flex;
    justify-content: center;
    align-items: center;
    outline: solid;
    outline-color: white;
    outline-width: 1px;
    outline-offset: -0.3rem;
}
.preetam-singh {
    font-family: "valorantFont", sans-serif;
    color: red;
    font-weight: 400;
}
.leaflet-name {
    font-family: "valorantFont", sans-serif;
    color: green;
}
.body {
    display: flex;
    width: 100vw;
    margin-top: 5vh;
    height: 95vh;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}
.map {
    height: 95vh;
    width: 50vw;
    z-index: 8;
}

.info-button {
    background-color: red;
    color: white;
    padding: 0.5rem;
    font-size: 2.1rem;
    border-bottom-right-radius: 1rem;
    font-weight: 300;
    border-top-left-radius: 1rem;
    outline: solid;
    outline-color: red;
    outline-offset: 0.5rem;
    outline-width: 1px;
}
.info-button:hover {
    transform: scale(1.1);
    transition: all 0.1s 0.1s;
    cursor: pointer;
}
.absolute-info {
    position: absolute;
    top: 0;
    display: none;
    width: 100vw;
    height: 100vh;
    justify-content: center;
    align-items: center;
    z-index: 10;
    background-color: rgba(0, 0, 0, 0.568);
}
.info-box {
    display: flex;
    background-color: white;
    justify-content: space-between;
    padding-top: 5rem;
    padding-bottom: 5rem;
    align-items: center;
    flex-direction: column;
    height: 100vh;
    width: 60vw;
    border-bottom-right-radius: 15rem;
    border-top-left-radius: 15rem;
    outline: solid;
    outline-color: red;
    outline-offset: -1rem;
    outline-width: 3px;

    overflow: hidden;
}
.close-info {
    display: flex;
    width: 90%;
    justify-content: flex-end;
    align-items: center;
}
.close-button {
    background-color: red;
    font-family: "Rubik", sans-serif;
    color: white;
    padding: 0.5rem;
    font-size: 2rem;
    border-bottom-right-radius: 1rem;
    font-weight: 400;
    border-top-left-radius: 1rem;
    outline: solid;
    outline-color: red;
    outline-offset: 0.5rem;
    outline-width: 1px;
}
.close-button:hover {
    transform: scale(1.1);
    transition: all 0.1s 0.1s;
    cursor: pointer;
}
.info-box-paragraph {
    text-align: center;
    font-family: "Rubik", sans-serif;
    color: white;
    font-size: 2.1rem;
    width: 60%;
    /* background-color: rgba(0, 0, 0, 0.733); */
    background-color: red;
    padding: 2rem;
    border-bottom-right-radius: 5rem;
    border-top-left-radius: 5rem;
    font-weight: 300;
}
.credit {
    font-family: "valorantFont", sans-serif;
    text-align: center;
    font-size: 1.3rem;
    font-weight: 300;
    display: flex;
    justify-content: center;
    padding-left: 2rem;
    padding-right: 2rem;
    gap: 5px;
    align-items: flex-end;
    background-color: rgb(190, 190, 190);
    height: 4vh;
    border-top-left-radius: 1rem;
    border-bottom-right-radius: 1rem;
}
.panel {
    width: 50vw;
    height: 90vh;
    margin-top: 5rem;
    padding-top: 5vh;
    background-color: white;
    z-index: 9;
    border-top-right-radius: 5rem;
    border-bottom-right-radius: 5rem;
    gap: 12vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    transition: all 0.5s 0.1s;
    outline: solid;
    outline-color: red;
    outline-width: 2px;
    outline-offset: -0.5rem;
}
.panel-get-position {
    color: white;
    font-family: "Rubik", sans-serif;
    background-color: red;
    padding-top: 0.5rem;
    font-weight: 300;
    font-size: 5vh;
    outline: solid;
    outline-color: red;
    outline-offset: 0.5rem;
    outline-width: 3px;
    transition: all 0.5s 0.05s;
    border-bottom-right-radius: 3rem;
    border-top-left-radius: 3rem;
    height: 8vh;
    text-align: center;
    width: 30vw;
}
.panel-get-position:hover {
    cursor: pointer;
    transform: scale(1.1);
    transition: all 0.5s 0.05s;
}
.form-div {
    width: 100%;
    justify-content: center;
    align-items: center;
    height: 8vh;
}
.position-form {
    display: flex;
    gap: 3rem;
    justify-content: space-evenly;
    width: 100%;
}
.text-input {
    line-height: 1.5;
    padding-top: 0.25rem;
    width: 50%;
    border-top-left-radius: 1rem;
    border-bottom-right-radius: 1rem;
    padding-left: 1rem;
}
.submitButton {
    background-color: red;
    color: white;
    padding: 0.5rem;
    font-size: 2.1rem;
    border-bottom-right-radius: 1rem;
    font-weight: 300;
    border-top-left-radius: 1rem;
    outline: solid;
    outline-color: red;
    outline-offset: 0.5rem;
    outline-width: 1px;
    transition: all 0.1s 0.1s;
    border-color: rgba(255, 255, 255, 0);
    border-style: solid;
}
.submitButton:hover {
    transform: scale(1.1);
    transition: all 0.1s 0.1s;
    cursor: pointer;
}
.popup {
    color: red;
}
.logs {
    color: white;
    font-family: "valorantFont", sans-serif;
    font-size: 3rem;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
}
.logs-header {
    text-align: center;
    background-color: black;
    color: white;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    border-radius: 5rem;
    width: 100%;
    height: 5vh;
    font-size: 4vh;
}
.logs-body {
    overflow: scroll;
    padding: 2rem;
    height: 60vh;
}
.pane {
    margin-top: 3rem;
    font-family: "Rubik", sans-serif;
    font-size: 2rem;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    outline: solid;
    outline-color: red;
    border-color: black;
    border-style: solid;
    border-width: 1px;
    outline-offset: 0.5rem;
    outline-width: 1px;
    transition: all 0.1s 0.1s;
    background-color: white;
    color: black;
    border-bottom-right-radius: 2rem;
    border-top-left-radius: 2rem;
    /* height: 4rem; */
}
.note-text {
    width: 100%;
    text-align: left;
}
.note {
    width: 70%;
    color: black;
    background-color: white;
    border-radius: 0.5rem;
    font-size: 1.75rem;
}
.coords {
    width: 35%;
    font-weight: 500;
    font-size: 1.75rem;
}
.coords-text {
    width: 100%;
    text-align: right;
}
.delete-button {
    width: 5%;
    transition: all 0.5s 0.1s;
    padding-left: 0.5rem;
    /* background-color: red; */
    height: 100%;
}
.delete-button:hover {
    transform: scale(1.1);
    transition: all 0.1s 0.1s;
    cursor: pointer;
}
.note-imp {
    width: 100%;
    font-family: "Rubik";
    font-size: 2rem;
    background-color: lightgrey;
    padding-left: 1rem;
    padding-right: 1rem;
    color: black;
    font-weight: 300;
    text-align: center;
}
